.container5 {
    position: relative;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #f0f0f0;
    /* 背景顏色 */
    font-family: 'Comic Sans MS', cursive, sans-serif;
    /* 卡通風格字體 */
}

.cartoonButton {
    padding: 12px 24px;
    font-size: 18px;
    background-color: #8b5a2b;
    /* 木頭顏色 */
    color: #fff;
    /* 白色文字 */
    border: none;
    border-radius: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    outline: none;
    /* 移除默認的輪廓 */
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.3);
    /* 木頭顏色的陰影效果 */

    &:hover {
        background-color: #a07246;
        /* 按鈕懸停顏色 */
    }
}

.foundButton5 {
    margin-bottom: 20px;
    /* 按鈕間距 */
}

.endGameButton5 {
    margin-top: 20px;
    /* 按鈕間距 */
}